          <h3>ClonedPerson<iframe style="margin-left: 2px; margin-bottom:-5px;" frameborder="0" scrolling="0" width="91px" height="20px" src="https://ghbtns.com/github-btn.html?user=Yanan-Wang-cs&repo=ClonedPerson&type=star&count=true" ></iframe></h3>
          <div class="column">
            <div><img src="./assets/clonedperson.png" width="100%"/></div>
          </div>
          <p>The ClonedPerson is a more realistic virtual dataset. We proposed a pipeline to directly clone the whole outfits from real-world person images to virtual 3D characters, designed two cloning methods, namely registered clothes mapping and homogeneous cloth expansion, and proposed a similarity-diversity expansion strategy to scale up IDs. Finally, by rendering the cloned characters in Unity3D scenes, a more realistic virtual dataset called ClonedPerson is created, with 5,621 identities and 887,766 images. Experimental results show that the model trained on ClonedPerson has a better generalization performance, superior to that trained on other popular real-world and synthetic person re-identification datasets.</p>

          <h3>Randperson<iframe style="margin-left: 2px; margin-bottom:-5px;" frameborder="0" scrolling="0" width="91px" height="20px" src="https://ghbtns.com/github-btn.html?user=VideoObjectSearch&repo=RandPerson&type=star&count=true" ></iframe></h3>
          <div class="column">
            <div><img src="./assets/randperson.png" width="100%"/></div>
          </div>
          <p>The RandPerson is the first synthetic person re-identification dataset with a set-up similar to real video surveillance systems, with a camera network and multiple people moving at the same time. We design a method to generate a large number of random UV texture maps and use them to create different 3D clothing models. By training person re-identification models on these synthesized person images, we demonstrate, for the first time, that models trained on virtual data can generalize well to unseen target images, surpassing the models trained on various real-world datasets. </p>

